.mainBody {
  border: 3px;
  border-style: solid;
  display: flex;
  flex-direction: column;
}

.headerMiddle {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.footer {
  height: 80px;
  width: 100%;
  text-align: center;
}

.footer-icon {
  width: 60px;
  height: 60px;
  padding: 10px;
}

.middle-content {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  flex-direction: column;
  padding-left: 10px;
  padding-right: 10px;
}

.middle-icon {
  width: 70px;
  height: 70px;
  padding: 10px;
}

.flex-container-middle {
  flex-grow: 1;
}

.flex-container-top-content {
  flex-direction: column;
}

.flex-container-footer {
  display: flex;
  flex-wrap: nowrap;
  min-height: 80px;
}

.flex-container-header {
  display: flex;
  flex-wrap: nowrap;
  min-height: 30px;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 5px;
  align-items: center;
}

.flex-item-left {
  width: 80px;
  height: 80px;
}

.flex-item-right {
  flex: auto;
  padding: 10px;
  margin: auto;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.spin {
  transform-origin: center;
  animation: spin 8s linear infinite;
}
